import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Location } from '@angular/common';
import Swal from 'sweetalert2'
@Injectable({
  providedIn: 'root'
})

export class AppService {
  error(response_message: any): any {
    throw new Error("Method not implemented.");
  }

  httpOptions: { headers: any; };
  baseUrl = "http://3.6.100.194:3010/";// devlopmnet
  baseUrlCrop = 'http://3.6.100.194:3012/'
  websiteurl = 'http://3.6.100.194/usertestingweb/#/'
  evnvor = 'prod'
  constructor(
    public http: HttpClient,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private _location: Location
  ) {

    // if (this.evnvor == 'prod') {
    //   this.baseUrl = "http://3.20.114.64:3010/"; // live
    //   this.baseUrlCrop = 'http://3.20.114.64:3014/'
    //   this.websiteurl = 'http://3.20.114.64/onlineusertesting/#/'
    // }

    if (window.location.protocol == 'http:') { 
      
      this.baseUrl = "http://3.20.114.64:3010/"; // live
      this.baseUrlCrop = 'http://3.20.114.64:3014/'
      this.websiteurl = 'http://3.20.114.64/onlineusertesting/#/'
  }  
  else
        this.baseUrl = "https://onlineusertesting.com:3009/"; // live
        this.baseUrlCrop = 'https://onlineusertesting.com:3014/'
        this.websiteurl = 'https://onlineusertesting.com'
  }

  getApi(url): Observable<any> {
    this.httpOptions = {
      headers: new HttpHeaders({
        "Content-Type": "application/json",
      }),
    }
    return this.http.get(this.baseUrl + url, this.httpOptions);
  }

  getApiWithAuth(url): Observable<any> {
    this.httpOptions = {
      headers: new HttpHeaders({
        "Content-Type": "application/json",
        'Authorization': localStorage.getItem('token')
      }),
    }
    return this.http.get(this.baseUrl + url, this.httpOptions);
  }

  formdataApi(url, data): Observable<any> {
    var httpOptions;
    httpOptions = {
      headers: new HttpHeaders({ 'Authorization': localStorage.getItem('token') }),

    }
    return this.http.post((this.baseUrl + url), data, httpOptions)
  }


  formdataApiCrop(url, data): Observable<any> {
    var httpOptions;
    httpOptions = {
      headers: new HttpHeaders({ 'Authorization': localStorage.getItem('token') }),

    }
    return this.http.post((this.baseUrlCrop + url), data, httpOptions)
  }
  succ(msg) {
    this.toastr.success(msg);
  }

  sweetAlert(msg) {
    Swal.fire(msg);
  }
  err(msg) {
    this.toastr.error(msg);
  }
  showSpinner() {
    //  this.loader=true
  }
  hideSpinner() {
    this.spinner.hide()
  }
  backClicked() {
    this._location.back();
  }


  postApi(url, data, isHeader): Observable<any> {
    if (!isHeader) {
      this.httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json" }),
      }
      return this.http.post(this.baseUrl + url, data);
    }
    else {
      this.httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json", 'token': localStorage.getItem('LOGINTOKEN'), '_id': localStorage.getItem('_id') }),
      }
      return this.http.post(this.baseUrl + url, data, this.httpOptions);
    }
  }

  postApiCrop(url, data, isHeader): Observable<any> {
    if (!isHeader) {
      this.httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json" }),
      }
      return this.http.post(this.baseUrlCrop + url, data);
    }
    else {
      this.httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json", 'token': localStorage.getItem('LOGINTOKEN'), '_id': localStorage.getItem('_id') }),
      }
      return this.http.post(this.baseUrl + url, data, this.httpOptions);
    }
  }

  postApiWithAuth(url, data, isHeader): Observable<any> {
    if (!isHeader) {
      this.httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json" }),
      }
    } else {
      this.httpOptions = {
        headers: new HttpHeaders({
          "Content-Type": "application/json",
          'Authorization': localStorage.getItem('token')
        })
      }
    }
    return this.http.post(this.baseUrl + url, data, this.httpOptions);
  }

  exportFileData(url) {
    window.open(this.baseUrl + 'web/exportFile?fileName=' + url)
  }

}
