import { Component, OnInit } from '@angular/core';
declare let $:any;

@Component({
  selector: 'app-recording-view',
  templateUrl: './recording-view.component.html',
  styleUrls: ['./recording-view.component.css']
})
export class RecordingViewComponent implements OnInit {
  notebox = false;
  inactive = true;
  startTime: Date;
  stopTime: Date;
  active: boolean = false;
  
  constructor() { }

  ngOnInit(): void {
    this.start();
   
    $('body').on('click', '.next', function() { 
      var id = $('.content:visible').data('id');
      var nextId = $('.content:visible').data('id')+1;
      $('[data-id="'+id+'"]').hide();
      $('[data-id="'+nextId+'"]').show();
      $('.prev_btn').prop('disabled',false);
      
      if(nextId == 9){
        $(this).prop('disabled',true);
      }
  });
  
  $('body').on('click', '.prev_btn', function() { 
      var id = $('.content:visible').data('id');
      var prevId = $('.content:visible').data('id')-1;
      $('[data-id="'+id+'"]').hide();
      $('[data-id="'+prevId+'"]').show();
      $('.next').prop('disabled',false);

      
      if(prevId == 1){
          $(this).prop('disabled',true);
      }    
  });
  }

  showNotebox() {
    this.notebox = true;
  }

  closeNotebox() {
    this.notebox = false;
  }
  
  
  get display() { return (this.startTime && this.stopTime) ? +this.stopTime - +this.startTime : 0 }

  timer() {
    if (this.active) {
      this.stopTime = new Date()
      setTimeout(()=>{
        this.timer()
      }, 1000)
    }
  }

  start() {
    this.startTime = new Date()
    this.stopTime = this.stopTime
    this.active = true;
    this.inactive = true;
    this.timer()
  }

  stop() {
    this.stopTime = new Date()
    this.active = false;
    this.inactive = false;
  }

}
