import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../app.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-demos',
  templateUrl: './demos.component.html',
  styleUrls: ['./demos.component.css']
})
export class DemosComponent implements OnInit {

  LoginForm: FormGroup
  LoginForm2: FormGroup
  formInvalid = false
  type = 'password'
  constructor(
    private route: Router,
    private service: AppService,
  ) { }

  ngOnInit(): void {
    this.LoginForm = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.pattern('^[a-zA-Z]+([.-]?[a-zA-Z0-9]+)*@([a-zA-Z]+([.-]?[a-zA-Z]))[.]{1}[a-zA-Z]{2,}$')]),
    });
    this.LoginForm2 = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.pattern('^[a-zA-Z]+([.-]?[a-zA-Z0-9]+)*@([a-zA-Z]+([.-]?[a-zA-Z]))[.]{1}[a-zA-Z]{2,}$')]),
    });
  }


  onSubmitFirst() {
    if (!this.LoginForm2.value.email) {
      this.service.err('Please enter email id')
      return

    }
    if (!this.LoginForm.valid) {
      this.service.err('Please enter valid email id')
    } else {

      console.log(this.LoginForm.value)
      let apiData = {
        "email": this.LoginForm.value.email,
      }

      this.service.postApi('web/createPaidUser', apiData, 0).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.service.succ(success.message)
          this.LoginForm.reset()
        }
        else {
          this.service.err(success.message)
        }
      }, error => {
        this.service.err("Something went wrong")
      })

    }


  }


  onSubmitSecond() {
    if (!this.LoginForm2.value.email) {
      this.service.err('Please enter email id')
      return

    }
    if (!this.LoginForm2.valid) {
      this.service.err('Please enter valid email id')
    } else {
      console.log(this.LoginForm2.value)
      let apiData = {
        "email": this.LoginForm2.value.email,
      }

      this.service.postApi('web/createPaidUser', apiData, 0).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.service.succ(success.message)
          this.LoginForm2.reset()
        }
        else {
          this.service.err(success.message)
        }
      }, error => {
        this.service.err("Something went wrong")
      })


    }


  }

}
