import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { HomeComponent } from './home/home.component';
import { ProductComponent } from './product/product.component';
import { PricingComponent } from './pricing/pricing.component';
import { DemosComponent } from './demos/demos.component';
import { CustomerComponent } from './customer/customer.component';
import { LoginComponent } from './login/login.component';
import { GetPaidComponent } from './get-paid/get-paid.component';
import { HelpComponent } from './help/help.component';
import { RegisterComponent } from './register/register.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component'; 
import { AboutComponent } from './about/about.component';
import { NewsComponent } from './news/news.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PasswordSetComponent } from './password-set/password-set.component';
import { OrganizationLoginComponent } from './organization-login/organization-login.component';
import { RecordingViewComponent } from './user/recording-view/recording-view.component';
import { PrivacyPolicyComponent } from './privacy-policy/privacy-policy.component';
import { TermsComponent } from './terms/terms.component';
import { ServiceAgreementComponent } from './service-agreement/service-agreement.component';
import { CopyrightsComponent } from './copyrights/copyrights.component';
import { TermsAndConditionsComponent } from './user/terms-and-conditions/terms-and-conditions.component';
import { ContactUsComponent } from './user/contact-us/contact-us.component';
import { AboutUsComponent } from './user/about-us/about-us.component';


const routes: Routes = [
  
  { path: '', component: HomeComponent }, 
  { path: 'product', component: ProductComponent }, 
  { path: 'pricing', component: PricingComponent }, 
  { path: 'demos', component: DemosComponent }, 
  { path: 'customer', component: CustomerComponent }, 
  { path: 'login', component: LoginComponent }, 
  { path: 'get-paid', component: GetPaidComponent }, 
  { path: 'help', component: HelpComponent }, 
  { path: 'register', component: RegisterComponent }, 
  { path: 'forgot', component: ForgotPasswordComponent },  
  { path: 'about', component: AboutComponent }, 
  { path: 'news', component: NewsComponent }, 
  { path: 'dashboard', component: DashboardComponent }, 
  { path: 'password', component: PasswordSetComponent }, 
  { path: 'organizationlogin', component: OrganizationLoginComponent }, 
  { path: 'user/recording-view', component:RecordingViewComponent},
  { path: 'privacy-policy', component: PrivacyPolicyComponent }, 
  { path: 'terms', component: TermsComponent }, 
  { path: 'service-agreement', component: ServiceAgreementComponent }, 
  { path: 'copyrights', component: CopyrightsComponent }, 
  { path: 'terms-and-condition', component: TermsAndConditionsComponent }, 
  { path: 'contact-us', component: ContactUsComponent }, 
  { path: 'about-us', component: AboutUsComponent }, 
  
  { path: 'user', loadChildren: () => import('./user/user.module').then(m => m.UserModule) }, 

];

@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash:true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
