import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../app.service';
import { Router } from '@angular/router';
declare var $
@Component({
  selector: 'app-get-paid',
  templateUrl: './get-paid.component.html',
  styleUrls: ['./get-paid.component.css']
})
export class GetPaidComponent implements OnInit {

  LoginForm: FormGroup
  LoginForm2: FormGroup
  formInvalid = false
  type = 'password'
  constructor(
    private route: Router,
    private service: AppService,
  ) { }

  ngOnInit(): void {
    this.LoginForm = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.pattern('^[a-zA-Z]+([.-]?[a-zA-Z0-9]+)*@([a-zA-Z]+([.-]?[a-zA-Z]))[.]{1}[a-zA-Z]{2,}$')]),
    });
    this.LoginForm2 = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.pattern('^[a-zA-Z]+([.-]?[a-zA-Z0-9]+)*@([a-zA-Z]+([.-]?[a-zA-Z]))[.]{1}[a-zA-Z]{2,}$')]),
    });


    // accordian
$(document).ready(function() {
  $(".set > a").on("click", function() {
      if ($(this).hasClass("active")) {
          $(this).removeClass("active");
          $(this)
              .siblings(".content")
              .slideUp(200);
          $(".set > a i")
              .removeClass("fa-minus")
              .addClass("fa-plus");
      } else {
          $(".set > a i")
              .removeClass("fa-minus")
              .addClass("fa-plus");
          $(this)
              .find("i")
              .removeClass("fa-plus")
              .addClass("fa-minus");
          $(".set > a").removeClass("active");
          $(this).addClass("active");
          $(".content").slideUp(200);
          $(this)
              .siblings(".content")
              .slideDown(200);
      }
  });
});
  }


  onSubmitFirst() {
    if (!this.LoginForm.value.email) {
      this.service.err('Please enter email id')
      return

    }
    if (!this.LoginForm.valid) {
      this.service.err('Please enter valid email id')
    } else {

      console.log(this.LoginForm.value)
      let apiData = {
        "email": this.LoginForm.value.email,
      }

      this.service.postApi('web/createPaidUser', apiData, 0).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          $('#apply').modal('show')
          // this.service.succ(success.message)
          this.LoginForm.reset()
        }
        else {
          this.service.err(success.message)
        }
      }, error => {
        this.service.err("Something went wrong")
      })

    }


  }


  onSubmitSecond() {
    if (!this.LoginForm2.value.email) {
      this.service.err('Please enter email id')
      return

    }
    if (!this.LoginForm2.valid) {
      this.service.err('Please enter valid email id')
    } else {
      console.log(this.LoginForm2.value)
      let apiData = {
        "email": this.LoginForm2.value.email,
      }

      this.service.postApi('web/createPaidUser', apiData, 0).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.service.succ(success.message)
          this.LoginForm2.reset()
        }
        else {
          this.service.err(success.message)
        }
      }, error => {
        this.service.err("Something went wrong")
      })


    }


  }

}
